@use 'variables' as v;

@mixin telefono {
    @media (min-width:v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width:v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width:v.$desktop) {
        @content;
    }
}

@mixin tabletMediana {
    @media (min-width:768px) and (max-width:1024px){
        @content;
    }
}