.text-center {
  text-align: center;
}

/* Header */
.header {
  padding-top: 1px;
  padding: 2rem;
  text-align: center;
  background-image: url("/build/img/header_bg.svg");
  background-repeat: no-repeat;
  background-size: 10rem;
  background-position: top right;
  overflow: hidden;
}
@media (min-width: 768px) {
  .header {
    background-size: 79rem;
    margin: 0;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .header {
    background-size: 58rem;
  }
}
.header .barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header .barra {
    margin: 0 auto;
    width: 70%;
    align-items: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 2rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .header .barra {
    justify-content: space-around;
  }
}
.header .barra .logo {
  width: 10rem;
  height: 10rem;
  background-image: url(/build/img/logo.svg);
  background-size: 10rem;
  background-repeat: no-repeat;
  /* margin-left: 5rem; */
  transition: transform 1s;
}
.header .barra .logo:hover {
  cursor: pointer;
  transform: rotate(360deg);
}
@media (min-width: 768px) {
  .header .barra .logo {
    margin: 0;
  }
}
.header .barra .navegacion {
  color: #6834a6;
}
.header .barra .navegacion ul {
  margin-right: 3rem;
}
@media (min-width: 768px) {
  .header .barra .navegacion ul {
    display: flex;
    gap: 2rem;
    width: 52rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .header .barra .navegacion ul {
    justify-content: flex-end;
  }
}
.header .barra .navegacion ul li a {
  color: #6834a6;
}
@media (min-width: 768px) {
  .header .barra .navegacion ul li a {
    color: #FFF;
    margin: 1rem;
  }
}
.header .barra .navegacion ul li a:hover {
  color: #765e92;
}
.header .central {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header .central {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    width: 70%;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .header .central {
    width: 100%;
    grid-template-columns: 0.2fr 1fr;
  }
}
@media (min-width: 768px) {
  .header .central h1 {
    text-align: left;
  }
}
.header .central input {
  all: unset;
  background-color: #6834a6;
  width: 100%;
  padding: 1rem;
  color: #FFF;
  margin-bottom: 5rem;
  cursor: pointer;
}
.header .central input:hover {
  background-color: #765e92;
}
@media (min-width: 768px) {
  .header .central input {
    width: 50%;
    margin-right: 31rem;
  }
}
.header .central .imagen-header {
  width: 50rem;
  height: 80rem;
  background-image: url("/build/img/app_1.svg");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: top center;
}
@media (min-width: 768px) {
  .header .central .imagen-header {
    margin-top: 3rem;
    width: 50rem;
    height: 50rem;
    background-size: contain;
    background-position: top left;
  }
}

/* Main */
.main {
  text-align: center;
}
.main .contenedor {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .main .contenedor {
    display: grid;
    grid-template-columns: 0.6fr 1fr;
    width: 100%;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .main .contenedor {
    display: flex;
  }
}
.main .contenedor .contenido {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  gap: 2rem;
  overflow: hidden;
  align-items: center;
  padding: 2rem;
}
@media (min-width: 768px) {
  .main .contenedor .contenido {
    grid-template-rows: unset;
    /* display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5rem; */
    padding: 7rem;
  }
  .main .contenedor .contenido p {
    text-align: left;
    padding: 0 2rem;
  }
}
.main .contenedor .contenido .facil, .main .contenedor .contenido .seguro, .main .contenedor .contenido .administrable {
  border: 1px solid gray;
  box-shadow: 0 0 10px gray;
  transition: transform 1s;
  width: 99%;
}
.main .contenedor .contenido .facil:hover, .main .contenedor .contenido .seguro:hover, .main .contenedor .contenido .administrable:hover {
  transform: scale(1.2);
}
.main .contenedor .contenido .facil h3, .main .contenedor .contenido .seguro h3, .main .contenedor .contenido .administrable h3 {
  color: #6834a6;
}
.main .contenedor .contenido :last-of-type {
  margin-bottom: 2rem;
}
.main .contenedor .imagen-main {
  margin-top: 5rem;
  max-width: 100%;
  height: 110rem;
  background-image: url("/build/img/app_2.svg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 100%;
}
@media (min-width: 768px) {
  .main .contenedor .imagen-main {
    margin: 0;
    width: 50rem;
    height: 80rem;
    background-size: 70%;
    background-position: top center;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .main .contenedor .imagen-main {
    width: 100%;
    background-size: 46%;
  }
}

/* Section nucleus safe */
.safeNucleus {
  background-color: #6834a6;
  text-align: center;
  color: #FFF;
  margin: 0;
  width: 100%;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .safeNucleus .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 0 13rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .safeNucleus .content {
    display: flex;
    grid-template-columns: unset;
    flex-wrap: wrap;
  }
}
.safeNucleus h1 {
  padding-top: 6rem;
  margin: 0;
  padding-bottom: 3rem;
  font-size: 7rem;
}
.safeNucleus .contenido {
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
}
.safeNucleus .contenido p {
  text-align: left;
  color: #000;
  padding: 2.5rem;
  background: #FFF;
  border: 1px solid gray;
  box-shadow: 0 0 2px gray;
  transition: transform 1s;
}
.safeNucleus .contenido p:hover {
  cursor: pointer;
  transform: scale(1.2);
}
.safeNucleus .contenido :last-of-type {
  margin-bottom: 5rem;
}
.safeNucleus .imagenSection1 {
  background-image: url(/build/img/seguridad.png);
  width: 100%;
  height: 40rem;
  background-size: 40rem;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .safeNucleus .imagenSection1 {
    height: 45rem;
    background-size: 46rem;
  }
}

.section2 {
  text-align: center;
  padding: 0 2rem;
  position: relative;
}
@media (min-width: 768px) {
  .section2 {
    margin: 0 auto;
    padding: 2rem;
  }
}
@media (min-width: 768px) {
  .section2 h1 {
    font-size: 8rem;
    margin-bottom: 17rem;
  }
}
.section2 .contenido {
  /* .imagen{
      width: 100%;
      height: 80rem;
      background-image: url('/build/img/app_3.svg');
      background-repeat:no-repeat ;
      background-size: 100%;
      background-position:center;

      @include m.tablet { 
          max-width: 100%;
          height:45rem;
          background-size:46rem;
      }
  } */
}
@media (min-width: 768px) {
  .section2 .contenido {
    /* display: grid;
    grid-template-columns: .5fr 1fr; */
    display: flex;
    padding: 0 6rem;
    width: 58%;
  }
}
@media (min-width: 1200px) {
  .section2 .contenido {
    width: 55%;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .section2 .contenido {
    flex-direction: column;
  }
}
.section2 .contenido h4 {
  font-size: 3rem;
  text-align: left;
  margin: 0;
}
.section2 .contenido .content {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .section2 .contenido .content {
    margin-left: 10rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .section2 .contenido .content {
    margin-left: 0;
  }
}
.section2 .contenido .content div {
  margin: 0 auto;
  width: 100%;
  padding: 0 1rem;
  border: 1px solid gray;
  box-shadow: 0 0 10px gray;
  transition: transform 1s;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .section2 .contenido .content div {
    display: flex;
    gap: 1rem;
    padding: 0 2rem;
    align-items: center;
  }
}
.section2 .contenido .content div:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.section2 .contenido .content h2 {
  color: #6834a6;
}
.section2 .contenido .content p {
  text-align: left;
}
.section2 .contenido .imagen {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .section2 .contenido .imagen {
    position: absolute;
    z-index: 1;
    width: 41rem;
    top: 18rem;
    right: 9rem;
    margin-top: 7rem;
    margin-right: 10rem;
  }
}
@media (min-width: 1200px) {
  .section2 .contenido .imagen {
    margin-top: 13rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .section2 .contenido .imagen {
    all: unset;
  }
}

.section3 {
  background-color: #6834a6;
  color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
}
@media (min-width: 768px) {
  .section3 {
    margin-top: 3rem;
  }
}
.section3 h1 {
  color: #FFF;
}
.section3 .testimonios {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .section3 .testimonios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.section3 .testimonios .testimonio {
  display: flex;
  flex-direction: column;
  border: 1px solid gray;
  background: white;
  padding: 2rem;
}
.section3 .testimonios .testimonio span {
  text-align: right;
  color: #6834a6;
  font-weight: 700;
}
.section3 .testimonios .testimonio .content {
  display: flex;
  gap: 1rem;
}
.section3 .testimonios .testimonio .content p {
  margin: 0;
}
.section3 .testimonios .testimonio img {
  width: 2.5rem;
  height: 2.5rem;
}

.footer {
  background-color: hsl(273deg, 92%, 29%);
  color: #FFF;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}
.footer .navFooter ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-right: 3rem;
}
@media (min-width: 768px) {
  .footer .navFooter ul {
    display: flex;
    flex-direction: row;
    margin: 0;
  }
}
.footer .navFooter ul a {
  color: #FFF;
}